@use "../base/variables" as var;
@use "../base/mixins" as mix;

#multi-player {
    height: 100vh;
    background-image: url(../../../build/img/bg-multiplayer.jpeg);
    display: flex;
    align-items: center;
    justify-content: flex-end;
    // background-size: contain;
    background-size: cover;

    .info {
        line-height: 1.2;
        padding: 4rem;
        width: 60rem;
        margin-right: 5rem;
        color: var.$blanco;
        background-color: rgba(0, 0, 0, 0.64);
        .h2-sub {
            text-align: start;

            &::before {
                top: 14rem;
                right: 3rem;
            }
        }
        p {
            font-size: 2rem;
            margin-top: 9.7rem;
        }
    }
    @include mix.desktop {
        .info {
            width: 60rem;
            margin: 0 auto;
        }
    }

    @include mix.tablet {
        background-position: -40rem center;

        .info {
            width: 90%;
            margin: 0 auto;
            .h2-sub {
                text-align: start;
                font-size: clamp(1.1rem, 3vw + 1rem, 4rem);

                &::before {
                    top: 7rem;
                }
            }
            p {
                margin-top: 8rem;
            }
        }

        @include mix.t672 {
            background-position: -60rem center;
            .info {
                p {
                    margin-top: 6rem;
                }

                .h2-sub::before {
                    right: 2rem;
                }
            }
        }
    }
}
