@use "../base/variables" as var;
@use "../base/mixins" as mix;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}
body {
    font-family: var.$fuente_principal;
    font-size: 1.6rem;
    line-height: 1.8;
    background-color: var.$negro;
    padding: 0;
    margin: 0;
}

h1,
h2,
h3 {
    font-family: var.$fuente_principal;

    font-weight: 300;
    text-align: center;
}

.h2-sub {
    color: var.$blanco;
    font-weight: 600;
    font-size: 4.7rem;
    position: relative;
    display: inline-block;
    padding: 0;
    margin: 0;
    margin-bottom: 0.7rem;
    &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 3.77rem;
        top: 7.7rem;

        transform: scale(0.5) skewY(-20deg) rotate(20deg);
        background-color: var.$verde;
    }
}
h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 3rem;
    color: var.$blanco;
}
h4 {
    font-size: 2.6rem;
}
