/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #000000;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3 {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 300;
  text-align: center;
}

.h2-sub {
  color: #ffffff;
  font-weight: 600;
  font-size: 4.7rem;
  position: relative;
  display: inline-block;
  padding: 0;
  margin: 0;
  margin-bottom: 0.7rem;
}
.h2-sub::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 3.77rem;
  top: 7.7rem;
  transform: scale(0.5) skewY(-20deg) rotate(20deg);
  background-color: #71b100;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
  color: #ffffff;
}

h4 {
  font-size: 2.6rem;
}

html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
   ========================================================================== */
/**
 * Remove the margin in all browsers.
 */
body {
  margin: 0;
}

/**
 * Render the `main` element consistently in IE.
 */
main {
  display: block;
}

/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Firefox, and Safari.
 */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
   ========================================================================== */
/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge and IE.
 */
hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
   ========================================================================== */
/**
 * Remove the gray background on active links in IE 10.
 */
a {
  background-color: transparent;
}

/**
 * 1. Remove the bottom border in Chrome 57-
 * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
 */
abbr[title] {
  border-bottom: none; /* 1 */
  text-decoration: underline; /* 2 */
  text-decoration: underline dotted; /* 2 */
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */
b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */
small {
  font-size: 80%;
}

/**
 * Prevent `sub` and `sup` elements from affecting the line height in
 * all browsers.
 */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Embedded content
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
img {
  border-style: none;
}

/* Forms
   ========================================================================== */
/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

/**
 * Show the overflow in IE.
 * 1. Show the overflow in Edge.
 */
button,
input { /* 1 */
  overflow: visible;
}

/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 * 1. Remove the inheritance of text transform in Firefox.
 */
button,
select { /* 1 */
  text-transform: none;
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
 * Remove the inner border and padding in Firefox.
 */
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus styles unset by the previous rule.
 */
button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Correct the padding in Firefox.
 */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * 1. Correct the text wrapping in Edge and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 * 3. Remove the padding so developers are not caught out when they zero out
 *    `fieldset` elements in all browsers.
 */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  padding: 0; /* 3 */
  white-space: normal; /* 1 */
}

/**
 * Add the correct vertical alignment in Chrome, Firefox, and Opera.
 */
progress {
  vertical-align: baseline;
}

/**
 * Remove the default vertical scrollbar in IE 10+.
 */
textarea {
  overflow: auto;
}

/**
 * 1. Add the correct box sizing in IE 10.
 * 2. Remove the padding in IE 10.
 */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Chrome.
 */
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
 * 1. Correct the odd appearance in Chrome and Safari.
 * 2. Correct the outline style in Safari.
 */
[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/* Interactive
   ========================================================================== */
/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */
details {
  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

header {
  background-image: url(../../../build/img/halo-infinite-cover-darkmode.webp);
  background-size: cover;
  background-position: center center;
  height: 100vh;
}

@media (max-width: 1458px) {
  header {
    height: 70vh;
  }
}
@media (max-width: 861px) {
  header {
    background-position: center 40px;
    height: 30vh;
  }
}
/*_____________

NAVBAR
_______________*/
.show {
  display: flex !important;
}

nav {
  display: flex;
  justify-content: space-between;
  padding: 0 5%;
  align-items: center;
}
nav ul {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}
nav ul img {
  margin-top: 0.4rem;
}
nav ul li > a {
  display: block;
  position: relative;
  padding: 2rem 1.5rem 1rem 1.5rem;
  text-decoration: none;
  color: #ffffff;
}
nav .mobile {
  display: none;
  width: 100%;
}

nav .mobile .header {
  background-color: black;
  display: flex;
  text-align: center;
  justify-content: space-between;
  flex-direction: row;
}
nav .mobile .header a {
  color: #ffffff;
  display: inline-flex;
  text-decoration: none;
  padding: 1rem;
}
nav .mobile .header #bmenu {
  border: none;
  background-color: transparent;
  cursor: pointer;
  color: #ffffff;
}
nav .mobile .links {
  display: none;
  flex-direction: column;
  position: relative;
  z-index: 200;
  background-color: #000000;
}
nav .mobile .links a {
  font-size: 1.8rem;
  display: block;
  padding: 1rem 2rem;
  text-decoration: none;
  color: #ffffff;
}
nav .mobile .links a:hover {
  font-size: 1.8rem;
  display: block;
  padding: 1rem 2rem;
  text-decoration: none;
  color: #000000;
  background-color: #ffffff;
}

nav ul li .link::before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0.2rem;
  transform: scaleX(0);
  transition: transform 0.3s ease;
  background-color: #ffffff;
}

nav ul li .link:hover::before {
  transform: scaleX(1);
}

nav .menu {
  display: none;
  position: absolute;
  flex-direction: column;
  min-width: 20rem;
  background-color: white;
  margin-top: 6.66rem;
}
nav .menu a {
  padding: 1rem;
  color: black;
  text-decoration: none;
}
nav .menu a:hover {
  background-color: #000000;
  color: #ffffff;
}

nav .more {
  display: none;
}

/*_____________

HEADER
_______________*/
@media (max-width: 1458px) {
  nav .link-hide {
    display: none;
  }
  nav .more {
    display: flex;
  }
}
@media (max-width: 861px) {
  nav {
    width: 100%;
    padding: 0;
  }
  nav ul {
    display: none;
  }
  nav .mobile {
    display: block;
  }
}
#banner {
  font-size: 3.1rem;
  text-align: center;
  padding: 4rem 4rem 1rem 4rem;
  width: 50rem;
  position: absolute;
  z-index: 100;
  top: 20rem;
  right: 10rem;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.5);
}

.green-button {
  text-transform: uppercase;
  border: 0;
  display: inline-flex;
  align-items: center;
  font-size: 1.4rem;
  padding: 1rem 4rem;
  background-color: #71b100;
  color: #000000;
  font-weight: bolder;
  text-decoration: none;
}
.green-button:hover {
  background-color: #5a8e00;
}

.green-link {
  text-transform: uppercase;
  border: 0;
  display: inline-flex;
  position: relative;
  align-items: center;
  font-size: 1.6rem;
  padding: 1rem 4rem;
  color: #71b100;
  font-weight: bolder;
  text-decoration: none;
}
.green-link::before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 0.2rem;
  transform: scaleX(0);
  transition: transform 0.3s ease;
  background-color: #71b100;
}

.green-link:hover::before {
  transform: scaleX(0.6);
}

@media (max-width: 1458px) {
  #banner {
    position: initial !important;
    z-index: 100;
    width: 100%;
  }
}
@media (max-width: 437px) {
  #banner .h2-sub::before {
    top: 16rem;
    left: 0.5rem;
  }
}
section .w-75 {
  width: 75%;
  margin: 0 auto;
  color: #ffffff;
  margin-top: 3.3rem;
}

h3 {
  font-size: 2rem;
}

#videos {
  text-align: center;
  padding: 5rem 0rem;
  background-image: url(../../../build/img/bg-videos.webp);
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
}
#videos #controls {
  display: flex;
  justify-content: center;
}
#videos #controls #prev,
#videos #controls #next {
  color: #ffffff;
  border: none;
  height: 5rem;
  width: 8rem;
  background-color: rgb(58, 58, 58);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 3s ease;
}
#videos #controls #prev:hover,
#videos #controls #next:hover {
  background-color: #000000;
}
#videos #current {
  width: 128rem;
  margin: 5rem auto;
}

#videos #videos-container {
  display: flex;
  gap: 10px;
  width: 100%;
  overflow: auto;
  justify-content: center;
}
#videos #videos-container .item {
  border: solid 0.1rem #00c3ff;
  cursor: pointer;
  overflow: hidden;
  min-width: 32rem;
  height: 18rem;
}
#videos #videos-container .item a {
  position: relative;
}
#videos #videos-container .item img {
  object-fit: contain;
  min-width: 32rem;
  height: 18rem;
}
#videos #videos-container .item:hover img {
  transition: opacity 0.7s ease;
  opacity: 0.7;
  object-fit: contain;
}

#videos a::before {
  font-family: "Material Icons";
  font-size: 2.4rem;
  content: "play_arrow";
  color: #ffffff;
  position: absolute;
  width: 5rem;
  height: 5rem;
  top: -9.5rem;
  left: 13.5rem;
  top: -95px;
  background-color: #000000;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
}

@media (max-width: 1458px) {
  #slider #current {
    width: 80%;
  }
  #slider #current iframe {
    height: 50rem;
  }
}
@media (max-width: 861px) {
  #slider #current {
    width: 90%;
  }
  #slider #current iframe {
    height: 30rem;
  }
}
#multi-player {
  height: 100vh;
  background-image: url(../../../build/img/bg-multiplayer.jpeg);
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background-size: cover;
}
#multi-player .info {
  line-height: 1.2;
  padding: 4rem;
  width: 60rem;
  margin-right: 5rem;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.64);
}
#multi-player .info .h2-sub {
  text-align: start;
}
#multi-player .info .h2-sub::before {
  top: 14rem;
  right: 3rem;
}
#multi-player .info p {
  font-size: 2rem;
  margin-top: 9.7rem;
}
@media (max-width: 1458px) {
  #multi-player .info {
    width: 60rem;
    margin: 0 auto;
  }
}
@media (max-width: 861px) {
  #multi-player {
    background-position: -40rem center;
  }
  #multi-player .info {
    width: 90%;
    margin: 0 auto;
  }
  #multi-player .info .h2-sub {
    text-align: start;
    font-size: clamp(1.1rem, 3vw + 1rem, 4rem);
  }
  #multi-player .info .h2-sub::before {
    top: 7rem;
  }
  #multi-player .info p {
    margin-top: 8rem;
  }
}
@media (max-width: 861px) and (max-width: 672px) {
  #multi-player {
    background-position: -60rem center;
  }
  #multi-player .info p {
    margin-top: 6rem;
  }
  #multi-player .info .h2-sub::before {
    right: 2rem;
  }
}

#get-the-game {
  text-align: center;
  padding: 5rem 0rem;
  color: #ffffff;
}
#get-the-game h2 {
  color: #ffffff;
}
#get-the-game .item-store {
  display: flex;
  width: 90%;
  justify-content: center;
}
#get-the-game .item-store .left {
  padding: 0 2rem;
  width: 100%;
}
#get-the-game .item-store .right {
  text-align: left;
  padding: 0 2rem;
  width: 60%;
}