@use "../base/variables" as var;
@use "../base/mixins" as mix;

header {
    background-image: url(../../../build/img/halo-infinite-cover-darkmode.webp);
    background-size: cover;
    background-position: center center;
    height: 100vh;
}

@include mix.desktop {
    header {
        height: 70vh;
    }
}

@include mix.tablet {
    header {
        background-position: center 40px;
        height: 30vh;
    }
}
