@use "../base/variables" as var;
@use "../base/mixins" as mix;

#get-the-game {
    text-align: center;
    padding: 5rem 0rem;
    color: var.$blanco;
    h2 {
        color: var.$blanco;
    }
    .item-store {
        display: flex;
        width: 90%;
        justify-content: center;
        .left {
            padding: 0 2rem;
            width: 100%;
        }
        .right {
            text-align: left;
            padding: 0 2rem;
            width: 60%;
        }
    }
}
