@use "../base/variables" as var;
@use "../base/mixins" as mix;
section {
    // h2 {
    //     font-weight: 600;
    //     font-size: 4.7rem;
    //     color: var.$blanco;
    // }
    .w-75 {
        width: 75%;
        margin: 0 auto;
        color: var.$blanco;
        margin-top: 3.3rem;
    }
}
h3 {
    font-size: 2rem;
}

#videos {
    text-align: center;
    padding: 5rem 0rem;
    background-image: url(../../../build/img/bg-videos.webp);
    background-position: bottom center;
    background-repeat: no-repeat;
    background-size: cover;
    #controls {
        display: flex;
        justify-content: center;
        #prev,
        #next {
            color: var.$blanco;
            border: none;
            height: 5rem;
            width: 8rem;
            background-color: var.$gris;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 3s ease;
            // La propiedad transition se encarga de definir una transición suave
            // de 3 segundos para todos los cambios que ocurran en las propiedades CSS de los elementos. Por ejemplo, si se cambia el color de fondo de gris a negro, este cambio se realizará de forma gradual y durará 3 segundos.
            &:hover {
                background-color: var.$negro;
            }
        }
    }

    #current {
        width: 128rem;
        margin: 5rem auto;
    }
}

// Videos 320 X 180

#videos #videos-container {
    display: flex;
    gap: 10px;
    width: 100%;
    overflow: auto;
    justify-content: center;
    .item {
        border: solid 0.1rem #00c3ff;
        cursor: pointer;
        overflow: hidden;
        min-width: 32rem;
        height: 18rem;
        a {
            position: relative;
        }
        img {
            object-fit: contain;
            min-width: 32rem;
            height: 18rem;
        }
        &:hover img {
            transition: opacity 0.7s ease;
            opacity: 0.7;
            object-fit: contain;
        }
    }
}

#videos a::before {
    font-family: "Material Icons";
    font-size: 2.4rem;
    content: "play_arrow";
    color: var.$blanco;
    position: absolute;
    width: 5rem;
    height: 5rem;
    top: -9.5rem;
    left: 13.5rem;
    top: -95px;
    background-color: var.$negro;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
}

// Responsive design

@include mix.desktop {
    #slider #current {
        width: 80%;

        iframe {
            height: 50rem;
        }
    }
}

@include mix.tablet {
    #slider #current {
        width: 90%;

        iframe {
            height: 30rem;
        }
    }
}
