@use "../base/variables" as var;
@use "../base/mixins" as mix;

/*_____________

NAVBAR
_______________*/

.show {
    display: flex !important;
}

nav {
    display: flex;
    justify-content: space-between;
    padding: 0 5%;

    align-items: center;

    ul {
        display: flex;
        list-style: none;
        padding: 0;
        margin: 0;
        img {
            margin-top: 0.4rem;
        }
    }

    ul li > a {
        display: block;
        position: relative;
        padding: 2rem 1.5rem 1rem 1.5rem;
        text-decoration: none;

        color: var.$blanco;
    }
    .mobile {
        display: none;
        width: 100%;
    }
}

nav .mobile {
    .header {
        background-color: black;
        display: flex;
        text-align: center;
        justify-content: space-between;
        flex-direction: row;
        a {
            color: var.$blanco;
            display: inline-flex;
            text-decoration: none;
            padding: 1rem;
        }
        #bmenu {
            border: none;
            background-color: transparent;
            cursor: pointer;
            color: var.$blanco;
        }
    }
    .links {
        display: none;
        flex-direction: column;
        position: relative;
        z-index: 200;
        background-color: var.$negro;
        a {
            font-size: 1.8rem;
            display: block;
            padding: 1rem 2rem;
            text-decoration: none;

            color: var.$blanco;
            &:hover {
                font-size: 1.8rem;
                display: block;
                padding: 1rem 2rem;
                text-decoration: none;
                color: var.$negro;
                background-color: var.$blanco;
            }
        }
    }
}

// EFECTO SUBRAYADO

nav ul li .link::before {
    content: "";
    position: absolute;
    display: block;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0.2rem;
    transform: scaleX(0);
    transition: transform 0.3s ease;
    background-color: var.$blanco;
}

nav ul li .link:hover::before {
    transform: scaleX(1);
}
// MENÚ MÓVIL
nav .menu {
    display: none;
    position: absolute;

    flex-direction: column;
    min-width: 20rem;
    background-color: white;
    margin-top: 6.66rem;
    a {
        padding: 1rem;
        color: black;
        text-decoration: none;
        &:hover {
            background-color: var.$negro;
            color: var.$blanco;
        }
    }
}

nav .more {
    display: none;
}

/*_____________

HEADER
_______________*/

@include mix.desktop {
    nav .link-hide {
        display: none;
    }
    nav .more {
        display: flex;
    }
}

@include mix.tablet {
    nav {
        width: 100%;
        padding: 0;

        ul {
            display: none;
        }
        .mobile {
            display: block;
        }
    }
}
