.green-button {
    text-transform: uppercase;
    border: 0;
    display: inline-flex;
    align-items: center;
    font-size: 1.4rem;
    padding: 1rem 4rem;
    background-color: var.$verde;
    color: var.$negro;
    font-weight: bolder;
    text-decoration: none;
    &:hover {
        background-color: var.$verdeOscuro;
    }
}

.green-link {
    text-transform: uppercase;

    border: 0;
    display: inline-flex;
    position: relative;
    align-items: center;
    font-size: 1.6rem;
    padding: 1rem 4rem;
    color: var.$verde;
    font-weight: bolder;
    text-decoration: none;
    &::before {
        content: "";
        position: absolute;
        display: block;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 0.2rem;
        transform: scaleX(0);
        transition: transform 0.3s ease;
        background-color: var.$verde;
    }
}

.green-link:hover::before {
    transform: scaleX(0.6);
}

@include mix.desktop {
    #banner {
        position: initial !important;
        z-index: 100;
        width: 100%;
    }
}

@include mix.t437 {
    #banner .h2-sub::before {
        top: 16rem;
        left: 0.5rem;
    }
}
