@use "../base/variables" as var;

/** Media Queries **/

@mixin tablet {
    @media (max-width: #{var.$tablet}) {
        @content;
    }
}

@mixin desktop {
    @media (max-width: #{var.$desktop}) {
        @content;
    }
}

@mixin t672 {
    @media (max-width: #{var.$t672}) {
        @content;
    }
}

@mixin t437 {
    @media (max-width: #{var.$t437}) {
        @content;
    }
}

@mixin grid($columnas, $espaciado) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}
